.warProd__navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem;
}

.warProd__navbar-links{
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

}

.warProd__navbar-links_logo{
    margin-right: 2rem;

}

.warProd__navbar-links_logo img{ 
   
    width: 128px;
    height: 128px;


}

.warProd__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.warProd__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.warProd__navbar-links_container p,
.warProd__navbar-sign p,
.warProd__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.warProd__navbar-sign button,
.warProd__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff; 
    background: #E4671F;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 5px;
}

.warProd__navbar-menu{
    margin-left: 1rem;    
    display: none;
    position: relative;

}

.warProd__navbar-menu svg {
    cursor: pointer;
}

.warProd__navbar-menu_container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

.warProd__navbar-menu_container p{
    margin: 1rem 0;
}
.warProd__navbar-menu_container-links-sign{
    display: none;
}

@media screen and (max-width: 1050px){
    .warProd__navbar-links_container{
        display: none;
    }

    .warProd__navbar-menu{
        display: flex;
    }
}

@media screen and (max-width: 700px){
    .warProd__navbar{
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 500px){
    .warProd__navbar{
        padding: 2rem;
    }

    .warProd__navbar-sign{
        display: none;
    }
    .warProd__navbar-menu_container {
        top: 20px;
    }

    .warProd__navbar-menu_container-links-sign{
        display: block;
    }

}