@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.warProd__blog-container_article {
    width: 100%;
    height: 100%;

    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}

.warProd__blog-container_article-image {
    width: 100%;
    height: 100%;
    background: var(--color-bg);
}

.warProd__blog-container_article-image img {
    width: 100%;
    height: 100%;
}

.warProd__blog-container_article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 1rem 1.5rem;
    height: 100%;
}

.warProd__blog-container_article-content p {
    font-family: var(--font-family);
    font-size: 11.65px;
    font-weight: 700;
    line-height: 35px;

    color: #fff;
}

.warProd__blog-container_article-content h3 {
    font-family: var(--font-family);
    font-size: 25.11px;
    font-weight: 800;
    line-height: 30.30px;
    
    color: #fff;
    margin-bottom: 5rem;
    cursor: pointer;
}

.warProd__blog-container_article-content p:last-child {
    cursor: pointer;
}


@media screen and (max-width: 550px) {
    .warProd__blog-container_article-content h3 {
        font-size: 18px;
        line-height: 25p;
    }
}
.warProd__brand{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.warProd__brand div{
    flex :1 1;
    max-width: 150px;
    min-width: 120px;
    margin: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;

}
.warProd__cta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;

    padding: 2rem;
    margin: 4rem;
    border-radius: 1rem;
    background: var(--gradient-bar)
}

.warProd__cta-content {
    display: flex;
    flex-direction: column;

    text-align: left;
    color: #fff;
}

.warProd__cta-content p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 30px;
    font-weight: 500;
}

.warProd__cta-content h3 {
    font-family: var(--font-family);
    font-size: 24px;
    line-height: 45px;
    font-weight: 800;
}

.warProd__cta-btn {
    display: flex;
    justify-content: center;
    align-items: center;

    margin-left: 2rem;
}

.warProd__cta-btn button {
    background: #000;
    color: #fff;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 18px;
    line-height: 30px;
    padding: 0.5rem 1rem;
    border-radius: 2rem;

    border: none;
    outline: none;
    cursor: pointer;
    min-width: 150px;
}

@media screen and (max-width: 650px) {
    .warProd__cta {
        flex-direction: column;
    }

    .warProd__cta-btn {
        margin: 2rem 0 0;
    }
}

@media screen and (max-width: 550px) {
    .warProd__cta {
        flex-direction: column;
        margin: 4rem 2rem;
    }

    .warProd__cta-content h3 {
        font-size: 18px;
        line-height: 32px;
    }

    .warProd__cta-btn button {
        font-size: 14px;
        line-height: 28px;
    }
}
.gpt3__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;

    margin: 1rem;
}

.gpt3__features-container__feature-title {
    flex: 1 1;
    max-width: 180px;
    margin-right: 2rem;
}

.gpt3__features-container__feature-title h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: #fff;
}

.gpt3__features-container__feature-title div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}

.gpt3__features-container_feature-text {
    flex: 2 1;
    max-width: 390px;
    display: flex;
}

.gpt3__features-container_feature-text p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}


.warProd__navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 2rem 6rem;
}

.warProd__navbar-links{
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

}

.warProd__navbar-links_logo{
    margin-right: 2rem;

}

.warProd__navbar-links_logo img{ 
   
    width: 128px;
    height: 128px;


}

.warProd__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.warProd__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.warProd__navbar-links_container p,
.warProd__navbar-sign p,
.warProd__navbar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;

    margin: 0 1rem;
    cursor: pointer;
}

.warProd__navbar-sign button,
.warProd__navbar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff; 
    background: #E4671F;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 5px;
}

.warProd__navbar-menu{
    margin-left: 1rem;    
    display: none;
    position: relative;

}

.warProd__navbar-menu svg {
    cursor: pointer;
}

.warProd__navbar-menu_container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background: var(--color-footer);
    padding: 2rem;
    position: absolute;
    right: 0;
    top: 40px;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);
}

.warProd__navbar-menu_container p{
    margin: 1rem 0;
}
.warProd__navbar-menu_container-links-sign{
    display: none;
}

@media screen and (max-width: 1050px){
    .warProd__navbar-links_container{
        display: none;
    }

    .warProd__navbar-menu{
        display: flex;
    }
}

@media screen and (max-width: 700px){
    .warProd__navbar{
        padding: 2rem 4rem;
    }
}

@media screen and (max-width: 500px){
    .warProd__navbar{
        padding: 2rem;
    }

    .warProd__navbar-sign{
        display: none;
    }
    .warProd__navbar-menu_container {
        top: 20px;
    }

    .warProd__navbar-menu_container-links-sign{
        display: block;
    }

}
.warProd__blog {
    display: flex;
    flex-direction: column;    
}

.warProd__blog-heading {
    width: 100%;
    text-align: left;

    margin-bottom: 5rem;
}

.warProd__blog-heading h1 {
    font-size: 62px;
    line-height: 75px;
    font-family: var(--font-family);
    font-weight: 800;
}

.warProd__blog-container {
    display: flex;
    flex-direction: row;
}

.warProd__blog-container_groupA {
    flex: 0.75 1;
    margin-right: 2rem;
}

.warProd__blog-container_groupB {
    flex: 1 1;

    display:grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 2rem;
}

@media screen and (max-width: 990px) {
    .warProd__blog-container {
        flex-direction: column-reverse;
    }


    .warProd__blog-container_groupA {
        margin: 2rem 0;
    }

    .warProd__blog-container_groupA .warProd__blog-container_article  {
       width: 48%;
    }

    .warProd__blog-container_groupA .warProd__blog-container_article-image {
        height: 250px;
    }
}

@media screen and (max-width: 700px) {
    .warProd__blog-container_groupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .warProd__blog-container_groupA .warProd__blog-container_article  {
       width: 100%;
    }

    .warProd__blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .warProd__blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

}
.warProd__features {
